main {
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.section-heading {
    display: grid;
    grid-template-columns: 1fr auto;
    align-items: center;
    margin-bottom: 50px;

    h1, h2 {
        margin: 0;
        font-weight: 400;
        color: $colour-heading;
        font-size: 2.25rem;
    }
    h2 {
        font-weight: 600;
        font-size: 1.8rem;
        padding-top: 5px;

        &::after {
            content: $icon-code-arrow;
            margin-left: 10px;
        }
        &:hover {
            text-decoration: underline;
            cursor: pointer;
        }
    }


    @media (max-width: $screen-small) {
        h2 {
            display: none;
        }
    }
}

.content-welcome {
    height: auto;
    margin-top: -50px;
    padding-top: 40px;
    padding-bottom: 40px;
    color: $white;
    background-color: $color-primary;

    button {
        @include buttonstyles($colour-primary-dark, true);
        border-radius: 5px;
        margin: 0px;
        width: 100%;        
    }
    h1 {
        font-size: 36px;
    }
    p {
        font-size: 16px;
        line-height: 25px;
        b {
            font-weight: 600;
        }
    }
    q {
        font-weight: 600;
        font-style: italic;
        font-size: 20px;
        line-height: 30px;
    }
    a {
        color: $white;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }

    .container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-areas:
        "section1 section2";
        gap: 30px;

        @media (max-width: $screen-medium) {
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 1fr;
            grid-template-areas:
            "section1"
            "section2";
        }
    }

    .welcome-section1,
    .welcome-section2 {
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        h1 {
            margin-top: 0;
            font-weight: 500;
        }

        &-buttons {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 30px;
            margin-top: auto;
        }
    }
    .welcome-section2 p {
        color: rgba($white, 0.75);
        font-weight: 500;
    }
    

    .stars {
        display: flex;
        margin-bottom: 25px;
        gap: 5px;
        font-size: 42px;
        color: $colour-gold-star;
    }

    #welcome-button3 {
        background-color: $colour-social-google;
    }
    #welcome-button4 {
        background-color: $colour-social-trustpilot;
    }
}

.content-news-container {
    display: flex;
    gap: 20px;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;

    .content-news-article {
        width: 370px;
        height: auto;
        border: 2px solid red;
        padding: 20px;
        .content-news-article_image {
            padding: 0;
        }

        &:hover {
            transition: 0.5s ease-in-out;
            margin-top: -10px;
        }
    }

    @media (max-width: $screen-large) {
        justify-content: space-between;
        .content-news-article {
            width: 48%;
            &:last-child {
                display: none;
            }
        }

    }
    @media (max-width: $screen-small) {
        justify-content: space-between;
        flex-direction: column;
        .content-news-article {
            width: 100%;
            &:hover {
                margin-top: 0px;
            }
        }
    }
}